







































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';

@Component
export default class Census extends Vue {
    @Prop() public tags!: { [x: string]: ITag };
    @Prop() public zones!: IZone[];
    @Prop() public renderTags!: { [x: string]: number };

    public value: number = -1;
    public info: any = {
        name: '',
        count: 0
    };

    public created() {
        this.info = this.getAllInfo();
    }

    private getAllInfo() {
        return {
            name: '',
            count: Object.values(this.renderTags).filter(v => v >= 0).length
        };
    }

    @Watch('renderTags', { deep: true })
    @Watch('value')
    private getInfo() {
        if (this.zones && this.renderTags) {
            if (this.value === -1) {
                this.info = this.getAllInfo();
            } else {
                const zone = this.zones.find(v => v.id === this.value);
                if (zone) {
                    this.$http
                        .get('/api/tag/getall', {
                            currentPage: 1,
                            pageSize: 1_0000_0000,
                            zone: this.value
                        })
                        .then(res => {
                            const list = res.pagedData.datas.filter(
                                v =>
                                    Reflect.has(this.renderTags, v.tagNo) &&
                                    this.renderTags[v.tagNo] >= 0
                            );

                            this.info = {
                                name:
                                    zone.name +
                                    (zone.name.endsWith('') ? '' : ''),
                                count: list.length
                            };
                        });
                }

                this.info = null;
            }
        }
    }
}
